import { handleActions } from 'redux-actions';
import {
  acMapSetMarkersList,
  acMapSetCurrentLatLng,
  acMapZoomIn,
  acMapZoomOut,
} from '../actions';
import { ZOOM } from '../../const/const_common';

const initialState = {
  markersList: [],
  currentLatLng: [],
  zoom: ZOOM.DEFAULT,
};

export default handleActions({
  [ acMapSetMarkersList ]: (state, { payload }) => ({
    ...state,
    markersList: payload.markersList,
  }),
  [ acMapSetCurrentLatLng ]: (state, { payload }) => ({
    ...state,
    currentLatLng: payload.currentLatLng,
  }),
  [ acMapZoomIn ]: state => ({
    ...state,
    zoom: state.zoom < ZOOM.MAX ? state.zoom + 1 : state.zoom,
  }),
  [ acMapZoomOut ]: state => ({
    ...state,
    zoom: state.zoom > ZOOM.MIN ? state.zoom - 1 : state.zoom,
  }),
}, initialState);
