import './Modal.scss';
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import classNames from 'classnames';
import { acModalHide } from '../../storage/actions';

const mapStateToProps = ({ modal }) => ({
  modalShow: modal.modalShow,
  title: modal.modalOptions.title,
  text: modal.modalOptions.text,
  okAction: modal.modalOptions.okAction,
  type: modal.modalOptions.type,
});

@connect(mapStateToProps)
class Modal extends PureComponent {
  handleClick = e => {
    e.stopPropagation();
    this.props.dispatch(acModalHide());
  };

  handleActionOk = e => {
    const { okAction } = this.props;
    e.stopPropagation();
    !!okAction && okAction();
  };

  render() {
    const { modalShow, title, text, type } = this.props;
    return (
      <div
        className={ classNames('Modal', modalShow && 'Modal--visible' ) }
        onClick={ this.handleClick }
      >
        <div className={ classNames('Modal--content', !!type && `Modal--${ type.toLowerCase() }`) }>
          <div
            key='title'
            className='Modal--title'
          >
            { title }
          </div>
          <div
            key='text'
            className='Modal--text'
          >
            { text }
          </div>
          <div
            key='actions'
            className='Modal--actions'
          >
            <button
              key='btnOk'
              className='Actions--button button-ok'
              onClick={ this.handleActionOk }
            >
              Ok
            </button>
            <button
              key='btnCancel'
              className='Actions--button button-cancel'
              onClick={ this.handleClick }
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Modal;
