import './Page.scss';
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

class Page extends PureComponent {
  static propTypes = {
    className : PropTypes.string,
  };

  static defaultProps = {
    className : null,
  };

  constructor(props) {
    super(props);
    this.classCSS = 'Page';
  }

  render() {
    const {
      className,
      children,
    } = this.props;
    return (
      <div className={ classNames(this.classCSS, !!className && className) }>
        { children }
      </div>
    )
  }
}

export default Page;
