import { handleActions } from 'redux-actions';
import { acModalHide, acModalShow } from '../actions';

const initialState = {
  modalShow: false,
  modalOptions: {},
};

export default handleActions({
  [ acModalShow ]: (state, { payload }) => ({
    ...state,
    modalShow: true,
    modalOptions: { ...payload },
  }),
  [ acModalHide ]: (state) => ({
    ...state,
    modalShow: false,
    modalOptions: {},
  }),
}, initialState);
