const isNotEmptyArray = v => Array.isArray(v) && v.length > 0;

const isNumber = v => (typeof v === 'number') && isFinite(v) && !isNaN(parseFloat(v));

const findArrayItemIndex = (arr, filter) => {
  let result = -1;
  if (!isNotEmptyArray(arr) || !filter) return result;
  for (let i = 0; i < arr.length; i++) {
    const keys = Object.keys(filter);
    let test = true;
    for (let j = 0; j < keys.length; j++) {
      test = (keys[j] in arr[i]) && (filter[keys[j]] === arr[i][keys[j]]);
      if (!test) break;
    }
    if (test) { result = i; break;}
  }
  return result;
};

const findArrayItem = (arr, filter) => {
  const i = findArrayItemIndex(arr, filter);
  return i === -1 ? null : arr[ i ];
};

export {
  isNotEmptyArray,
  isNumber,
  findArrayItemIndex,
  findArrayItem,
}