import './Aside.scss';
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

const Aside = props => {
  const {
    className,
    children
  } = props;
  const classCss = 'Aside';
  return (
    <div className={ classNames(classCss, !!className && className) }>
      { children }
    </div>
  )
};

Aside.propTypes = {
  className: PropTypes.string,
};

Aside.defaultProps = {
  className: null,
};

export default Aside;
