import './Header.scss';
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

const Header = props => {
  const {
    className,
    children
  } = props;
  const classCss = 'Header';
  return (
    <div className={ classNames(classCss, !!className && className) }>
      { children }
    </div>
  )
};

Header.propTypes = {
  className: PropTypes.string,
};

Header.defaultProps = {
  className: null,
};

export default Header;
