import './MarkersToolbar.scss';
import React from 'react';
import PropTypes from 'prop-types';
import AddIcon from '../icons/AddIcon';
import SaveIcon from '../icons/SaveIcon';

const classCSS = 'MarkersToolbar';

const MarkerToolbar = ({ cbAddMarker, markerDataFormShow, cbSaveMarkersList }) => {
  const handleAddMarker = () => {
    !!cbAddMarker && cbAddMarker();
  };

  const handleSaveList = () => {
    !!cbSaveMarkersList && cbSaveMarkersList();
  };

  const ACTIONS = [
    {
      name: 'addMarker',
      icon: AddIcon,
      title: 'Add new marker to list',
      disabled: markerDataFormShow,
      onClick: handleAddMarker,
    },
    {
      name: 'saveList',
      icon: SaveIcon,
      title: 'Save the list to local storage',
      disabled: false,
      onClick: handleSaveList,
    },
  ];

  return (
    <div className={ classCSS }>
      {
        ACTIONS.map(btn => {
          const { name, icon: Icon, onClick, disabled, title } = btn;
          return (
            <div key={ name } className='IconButton' title={ title }>
              <Icon
                height={ 24 }
                width={ 24 }
                onClick={ onClick }
                disabled={ disabled }
                title={ title }
              />
            </div>
          )
        })
      }
    </div>
  )
};

MarkerToolbar.propTypes = {
  cbAddMarker: PropTypes.func,
  markerDataFormShow: PropTypes.bool,
  cbSaveMarkersList: PropTypes.func,
};

MarkerToolbar.defaultProps = {
  cbAddMarker: null,
  markerDataFormShow: false,
  cbSaveMarkersList: null,
};

export default MarkerToolbar;
