import { handleActions } from 'redux-actions';
import {
  acUiMarkerDataFormShow,
  acUiMarkerDataFormHide,
  acUiMarkerDataFormSetMarker,
} from '../actions';

const initialState = {
  markerDataForm: {
    show: false,
    marker: {
      id: -1,
      name: '',
      latitude: 0,
      longitude: 0,
      description: '',
    }
  },
};

export default handleActions({
  [ acUiMarkerDataFormShow ]: (state, { payload }) => ({
    ...state,
    markerDataForm: { ...state.markerDataForm, ...payload, show: true },
  }),
  [ acUiMarkerDataFormHide ]: (state) => ({
    ...state,
    markerDataForm: { ...initialState.markerDataForm, show: false },
  }),
  [ acUiMarkerDataFormSetMarker ]: ( state, { payload }) => ({
    ...state,
    markerDataForm: {
      ...state.markerDataForm,
      marker: { ...payload },
    }
  }),
}, initialState);
