const COLOR_TYPE = {
  PRIMARY        : 'PRIMARY',
  SECONDARY      : 'SECONDARY',
  DEFAULT        : 'DEFAULT',
  CONTRAST_WHITE : 'CONTRAST_WHITE',
  CONTRAST_BLACK : 'CONTRAST_BLACK',
  TRANSPARENT    : 'TRANSPARENT',
};

const COLORS = {
  [ COLOR_TYPE.PRIMARY ]        : { r: 24, g: 116, b: 205, a: 1, contrast: false },
  [ COLOR_TYPE.SECONDARY ]      : { r: 255, g: 0, b: 0, a: 1, contrast: false },
  [ COLOR_TYPE.DEFAULT ]        : { r: 0, g: 0, b: 0, a: 1, contrast: false },
  [ COLOR_TYPE.CONTRAST_WHITE ] : { r: 255, g: 255, b: 255, a: 1, contrast: true },
  [ COLOR_TYPE.CONTRAST_BLACK ] : { r: 0, g: 0, b: 0, a: 1, contrast: true },
  [ COLOR_TYPE.TRANSPARENT ]    : { r: 255, g: 255, b: 255, a: 0, contrast: true },
};

// @parameter k - <Number> 0..100 - opacity coefficient
// @returns rgba(r, g, b, a)
const getColor = (colorType, k) => {
  const { r, g, b, a, contrast } = COLORS[ colorType ];
  return `rgba(${ r }, ${ g }, ${ b }, ${ contrast ? a : k / 100 })`;
};

export {
  COLOR_TYPE,
  getColor,
};
