import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { ICONS } from '../../const/const_common';
import { COLOR_TYPE, getColor } from '../../const/const_theme';

const { defaultHeight, defaultWidth, } = ICONS;

const AddIcon = ({ disabled, width, height, stroke, disabledStroke, onClick }) => {
  return (
    <svg
      className={ classNames('Icon CloseIcon', disabled && 'Icon-disabled') }
      xmlns ="http://www.w3.org/2000/svg"
      width={ width }
      height={ height }
      viewBox='0 0 16 16'
      onClick={ !!onClick && !disabled ? onClick : null }
    >
      <path
        d='M3 8 H13 M8 3 V13'
        stroke={ disabled ? disabledStroke : stroke }
        strokeWidth={ 1.5 }
        fill='none'
      />
    </svg>
  )
};

AddIcon.propTypes = {
  disabled       : PropTypes.bool,
  height         : PropTypes.oneOfType([
    PropTypes.string, PropTypes.number,
  ]),
  width          : PropTypes.oneOfType([
    PropTypes.string, PropTypes.number,
  ]),
  stroke         : PropTypes.string,
  disabledStroke : PropTypes.string,
  onClick        : PropTypes.func,
};

AddIcon.defaultProps = {
  disabled       : false,
  width          : defaultWidth,
  height         : defaultHeight,
  stroke         : getColor(COLOR_TYPE.DEFAULT, 100),
  disabledStroke : getColor(COLOR_TYPE.DEFAULT, 50),
  onClick        : null,
};

export default AddIcon;
