import './MarkerDataForm.scss';
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import CloseIcon from '../icons/CloseIcon';
import { COLOR_TYPE, getColor } from '../../const/const_theme';
import { MARKER_DESCRIPTION_MAX_LENGTH } from '../../const/const_common';

const classCSS = 'MarkerDataForm';

const MarkerDataForm = ({ isVisible, marker, cbChanged, cbClosed, cbSaveMarker, cbClearMarkerForm }) => {
  const { id, name, latitude, longitude, description, } = marker;

  const change = value => {
    !!cbChanged && cbChanged({ ...marker, ...value });
  };

  const filterValue = (name, value) => {
    const FILTER = {
      name: v => v.trim(),
      longitude: v => parseFloat(v),
      latitude: v => parseFloat(v),
      description: v => v.trim(),
    };
    return !!FILTER[ name ] ? FILTER[ name ](value) : value;
  };

  const handleChange = name => e => {
    let { value } = e.target;
    change({ [ name ]: value });
  };

  const handleBlur = name => e => {
    let value = filterValue(name, e.target.value);
    change({ [ name ]: value });
  };

  const handleClose = () => {
    !!cbClosed && cbClosed();
  };

  return (
    <div
      className={ classNames(classCSS, !isVisible && `${ classCSS }--hidden`) }
    >
      <div
        key='title'
        className={ `${ classCSS }--section form-title` }
      >
        <h3 className='section--title-content'>
          {
            id === -1
              ? 'Add new marker'
              : 'Edit marker'
          }
        </h3>
        <div className='icon-close--container'>
          <CloseIcon
            stroke={ getColor(COLOR_TYPE.DEFAULT, 50) }
            onClick={ handleClose }
          />
        </div>
      </div>
      <div
        key='content'
        className={ `${ classCSS }--section form-content` }
      >
        <div
          key='name'
          className={ `${ classCSS }--form-control` }
        >
          <label htmlFor='name'>Name</label>
          <input
            id='name'
            type="text"
            value={ name }
            autoComplete='off'
            autoFocus
            onChange={ handleChange('name') }
            onBlur={ handleBlur('name') }
          />
        </div>
        <div
          key='latitude'
          className={ `${ classCSS }--form-control` }
        >
          <label htmlFor='latitude'>Latitude</label>
          <input
            id='latitude'
            type="number"
            value={ latitude }
            onChange={ handleChange('latitude') }
            onBlur={ handleBlur('latitude') }
          />
        </div>
        <div
          key='longitude'
          className={ `${ classCSS }--form-control` }
        >
          <label htmlFor='longitude'>Longitude</label>
          <input
            id='longitude'
            type="number"
            value={ longitude }
            onChange={ handleChange('longitude') }
            onBlur={ handleBlur('longitude') }
          />
        </div>
        <div
          key='description'
          className={ `${ classCSS }--form-control description` }
        >
          <label htmlFor='description'>Description</label>
          <textarea
            id="description"
            maxLength={ MARKER_DESCRIPTION_MAX_LENGTH }
            value={ description }
            onChange={ handleChange('description') }
            onBlur={ handleBlur('description') }
          >
          </textarea>
        </div>
      </div>
      <div
        key='actions'
        className={ `${ classCSS }--section form-actions` }
      >
        <button
          key='btnOk'
          className={ `${ classCSS }--btn-ok` }
          onClick={ !!cbSaveMarker ? cbSaveMarker : null }
        >
          Ok
        </button>
        <button
          key='btn'
          className={ `${ classCSS }--btn-clear` }
          onClick={ !!cbClearMarkerForm ? cbClearMarkerForm : null }
        >
          Clear
        </button>
      </div>
    </div>
  )
};

MarkerDataForm.propTypes = {
  isVisible: PropTypes.bool,
  name: PropTypes.string,
  latitude: PropTypes.number,
  longitude: PropTypes.number,
  description: PropTypes.string,
  cbChanged: PropTypes.func,
  cbSaveMarker: PropTypes.func,
  cbClearMarkerForm: PropTypes.func,
};

MarkerDataForm.defaultProps = {
  isVisible: true,
  name: '',
  latitude: 0,
  longitude: 0,
  description: '',
  cbChanged: null,
  cbSaveMarker: null,
  cbClearMarkerForm: null,
};

export default MarkerDataForm;
