import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import Page from '../../Page/Page';
import Header from '../../Page/Header/Header';
import CentralSection from '../../Page/CentralSection/CentralSection';
import Aside from '../../Page/Aside/Aside';
import Wrapper from '../../Page/Wrapper/Wrapper';
import MapContainer from '../../MapContainer/MapContainer';
import MarkersList from '../../MarkersList/MarkersList';
import MarkerDataForm from '../../MarkerDataForm/MarkerDataForm';
import MarkersToolbar from '../../MarkersToolbar/MarkersToolbar';
import {
  acMapSetCurrentLatLng,
  acMapSetMarkersList, acMapZoomIn, acMapZoomOut,
  acModalHide,
  acModalShow,
  acUiMarkerDataFormHide, acUiMarkerDataFormSetMarker,
  acUiMarkerDataFormShow
} from '../../../storage/actions';
import { findArrayItem, } from '../../../utils/utils_common';
import { MARKER_DATA_FORM_HEIGHT, MODAL_TYPES } from '../../../const/const_common';

const mapStateToProps = ({ map, ui }) => ({
  markersList: map.markersList,
  currentLatLng: map.currentLatLng,
  mapZoom: map.zoom,

  markerDataForm: ui.markerDataForm,
});

@connect(mapStateToProps)
class MainPage extends PureComponent {

  componentDidMount() {
    let markersList = localStorage.getItem('markersList');
    try{
      markersList = JSON.parse(markersList);
      if (Array.isArray(markersList)) {
        this.props.dispatch(acMapSetMarkersList({ markersList }));
      }
    }
    catch ( e ) { /* nothing to do */ }

  }

  setMarkersList = markersList => {
    this.props.dispatch(acMapSetMarkersList({ markersList }));
  };

  cbMapContainerSetLatLng = latLng => {
    const { dispatch, markerDataForm } = this.props;
    const { marker } = markerDataForm;
    dispatch(acUiMarkerDataFormSetMarker({
      ...marker,
      latitude: latLng[ 0 ],
      longitude: latLng[ 1 ],
    }));
    dispatch(acMapSetCurrentLatLng({ currentLatLng: latLng }));
  };

  cbMapContainerZoom = value => {
    let action = value > 0
      ? acMapZoomIn
      : acMapZoomOut;
    this.props.dispatch(action());
  };

  onListItemClick = (e, item) => {
    // console.log('item: ', item);
  };

  cbMarkersListEditMarkerClicked = id => {
    const { dispatch, markersList } = this.props;
    const item = findArrayItem(markersList, { id });
    const marker = {
      id: item.id,
      name: item.name,
      latitude: item.latLng[ 0 ],
      longitude: item.latLng[ 1 ],
      description: item.description,
    };
    dispatch(acUiMarkerDataFormShow({ marker }));
  };

  showDeleteModal = id => {
    const { markersList, dispatch } = this.props;
    const item = findArrayItem(markersList, { id });
    dispatch(acModalShow({
      ...item,
      title: 'Please, confirm the action',
      text: `Are you sure you want to delete the marker: ${ item.name }`,
      okAction: () => {
        dispatch(acMapSetMarkersList({
          markersList: markersList.filter(marker => marker.id !== id)
        }));
        dispatch(acModalHide());
      },
      type: MODAL_TYPES.DELETE_CONFIRMATION,
    }));
  };

  cbMarkerDataFormChanged = marker => {
    this.props.dispatch(acUiMarkerDataFormSetMarker(marker));
  };

  cbMarkerDataFormClearMarkerForm = () => {
    const { marker } = this.props.markerDataForm;
    const newMarker = {
      name: '',
      latitude: 0,
      longitude: 0,
      description: '',
      selected: true,
      showMore: true,
    };
    this.props.dispatch(acUiMarkerDataFormSetMarker({ ...marker, ...newMarker }));
  };

  cbMarkerDataFormSaveMarker = () => {
    let { markersList, markerDataForm, dispatch } = this.props;
    const { id, name, latitude, longitude, description } = markerDataForm.marker;
    const newMarker = {
      id,
      name,
      latLng: [ latitude, longitude ],
      description,
      selected: true,
      showMore: true,
    };
    if (id === -1) {
      let newId = 0;
      markersList.forEach(marker => { newId = Math.max(newId, marker.id) });
      newId++;
      newMarker.id = newId;
      markersList = [ newMarker ].concat(markersList);
    }
    else {
      markersList = markersList.map(marker => {
        return marker.id === id ? { ...marker, ...newMarker } : { ...marker };
      });
    }
    dispatch(acMapSetMarkersList({ markersList }));
    this.cbMarkerDataFormClosed();
  };

  cbMarkerDataFormClosed = () => {
    this.props.dispatch(acUiMarkerDataFormHide());
  };

  cbToolbarAddMarker = () => {
    this.props.dispatch(acUiMarkerDataFormShow({}));
  };

  cbToolbarSaveMarkersList = () => {
    const { markersList, dispatch } = this.props;
    localStorage.clear();
    localStorage.setItem('markersList', JSON.stringify(markersList));
    dispatch(acModalShow({
      title: 'Success',
      text: 'The list has been saved to local storage',
      type: MODAL_TYPES.SUCCESS,
      okAction: () => { dispatch(acModalHide()); }
    }));
  };

  getMarkerListHeight = () => {
    const { show } = this.props.markerDataForm;
    return show ? `calc(100% - ${ MARKER_DATA_FORM_HEIGHT + 64 }px)` : 'calc(100% - 56px)';
  };

  render() {
    const {
      markersList,
      markerDataForm,
      currentLatLng,
      mapZoom,
    } = this.props;

    return (
      <Page className='MainPage'>
        <Header>
          My Geography
        </Header>
        <Wrapper>
          <Aside>
            <MarkersToolbar
              markerDataFormShow={ markerDataForm.show }
              cbAddMarker={ this.cbToolbarAddMarker }
              cbSaveMarkersList={ this.cbToolbarSaveMarkersList }
            />
            <MarkerDataForm
              isVisible={ markerDataForm.show }
              marker={ { ...markerDataForm.marker } }
              cbChanged={ this.cbMarkerDataFormChanged }
              cbClosed={ this.cbMarkerDataFormClosed }
              cbSaveMarker={ this.cbMarkerDataFormSaveMarker }
              cbClearMarkerForm={ this.cbMarkerDataFormClearMarkerForm }
            />
            <MarkersList
              height={ this.getMarkerListHeight() }
              markersList={ markersList }
              disabled={ markerDataForm.show }
              cbSetMarkerList = { this.setMarkersList }
              cbDeleteClicked = { this.showDeleteModal }
              cbEditClicked={ this.cbMarkersListEditMarkerClicked }
              onItemClick={ this.onListItemClick }
            />
          </Aside>
          <CentralSection>
            <MapContainer
              markersList={ markersList }
              currentLatLng={ currentLatLng }
              zoom={ mapZoom }
              cbSetMarkersList={ this.setMarkersList }
              cbSetLatLng={ this.cbMapContainerSetLatLng }
              cbZoom={ this.cbMapContainerZoom }
            />
          </CentralSection>
        </Wrapper>
      </Page>
    )
  }
}

export default MainPage;
