import './MarkersList.scss';
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import CloseIcon from '../icons/CloseIcon';
import EditIcon from '../icons/EditIcon';
import { isNotEmptyArray } from '../../utils/utils_common';
import { COLOR_TYPE, getColor } from '../../const/const_theme';

const classCSS = 'MarkersList';

const MarkersList = ({ className, height, disabled, markersList, cbSetMarkerList, cbDeleteClicked, cbEditClicked }) => {

  const toggleValue = (id, fieldName) => {
    !!cbSetMarkerList && cbSetMarkerList(
      markersList.map(marker => {
        const { id: markerId, [ fieldName ]: fieldValue } = marker;
        return { ...marker, [ fieldName ]: markerId === id ? !fieldValue : fieldValue };
      })
    );
  };

  const editItem = id => {
    !!cbEditClicked && cbEditClicked(id);
  };

  const deleteItem = id => {
    !!cbDeleteClicked && cbDeleteClicked(id);
  };

  const handleItemClick = id => e => {
    toggleValue(id, 'selected');
  };

  const handleShowMore = id => e => {
    e.stopPropagation();
    toggleValue(id, 'showMore');
  };

  const handleEditClick = id => e => {
    e.stopPropagation();
    editItem(id);
  };

  const handleDeleteClick = id => e => {
    e.stopPropagation();
    deleteItem(id);
  };

  const getItems = () => {
    return (
      <ul className={ `${ classCSS }--list` }>
        {
          markersList.map(marker => {
            const { id, name, latLng, selected, showMore, description } = marker;
            return (
              <li
                key={ id }
                className={ classNames(`${ classCSS }--list-item`, selected && 'list-item--selected') }
                title='Show/hide marker on the map'
                onClick={ handleItemClick(id) }
              >
                <span
                  key='name'
                  className={ `${ classCSS }--item-name` }
                >
                  { name }
                </span>
                <span
                  key='iconEdit'
                  className={ `${ classCSS }--icon-edit` }
                  title='Edit marker`s data'
                >
                  <EditIcon
                    stroke={ getColor(COLOR_TYPE.PRIMARY, 50) }
                    onClick={ handleEditClick(id) }
                  />
                </span>
                <span
                  key='iconDelete'
                  className={ `${ classCSS }--icon-delete` }
                  title='Delete marker'
                >
                  <CloseIcon
                    stroke={ getColor(COLOR_TYPE.SECONDARY, 50) }
                    onClick={ handleDeleteClick(id) }
                  />
                </span>
                {
                  showMore && (
                    <span key='details' className={ `${ classCSS }--item-details` }>
                      <span
                        key='coordinates'
                        className={ `${ classCSS }--details-section` }
                      >
                        <span
                          key='lat'
                          className={ `${ classCSS }--info-box` }
                        >
                          <span key='labelName' className={ `${ classCSS }--label-name` }>lat</span>
                          <span key='labelContent' className={ `${ classCSS }--label-content` }>{ latLng[0] }</span>
                        </span>
                        <span
                          key='lng'
                          className={ `${ classCSS }--info-box` }
                        >
                          <span key='labelName' className={ `${ classCSS }--label-name` }>long</span>
                          <span key='labelContent' className={ `${ classCSS }--label-content` }>{ latLng[1] }</span>
                        </span>
                      </span>
                      <span key='description' className={ `${ classCSS }--details-description-section` }>
                        <span key='descr' className={ `${ classCSS }--description-name` }>Description: </span>
                        { description }
                      </span>
                    </span>
                  )
                }
                <span
                  key='showMore'
                  className={ `${ classCSS }--label-link` }
                  title={ showMore ? 'Hide additional information' : 'Show additional information' }
                  onClick={ handleShowMore(id) }
                >
                  {
                    showMore ? 'Hide additional info' : 'Show more...'
                  }
                </span>
              </li>
            )
          })
        }
      </ul>
    )
  };

  return (
    <div
      className={ classNames(classCSS, !!className && className) }
      style={{ height }}
    >
      {
        isNotEmptyArray(markersList)
          ? getItems()
          : 'No data to display'
      }
      {
        disabled && (
          <div key='modal' className={ `${ classCSS }--modal` } />
        )
      }
    </div>
  )
};

MarkersList.propTypes = {
  className: PropTypes.string,
  disabled: PropTypes.bool,
  height: PropTypes.oneOfType([
    PropTypes.string, PropTypes.number,
  ]),
  markersList: PropTypes.arrayOf(PropTypes.object),
  cbSetMarkerList: PropTypes.func,
  cbDeleteClicked: PropTypes.func,
  cbEditClicked: PropTypes.func,
};

MarkersList.defaultProps = {
  className: null,
  disabled: false,
  height: '100%',
  markersList: [],
  cbSetMarkerList: null,
  cbDeleteClicked: null,
  cbEditClicked: null,
};

export default MarkersList;
