import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { ICONS } from '../../const/const_common';
import { COLOR_TYPE, getColor } from '../../const/const_theme';

const { defaultHeight, defaultWidth, } = ICONS;

const EditIcon = ({ disabled, width, height, stroke, disabledStroke, onClick }) => {
  return (
    <svg
      className={ classNames('Icon EditIcon', disabled && 'Icon-disabled') }
      xmlns ="http://www.w3.org/2000/svg"
      width={ width }
      height={ height }
      viewBox='0 0 16 16'
      onClick={ !!onClick && !disabled ? onClick : null }
    >
      <g>
        <path
          d='M1 1 H15 V12 L12 15 H1 Z M15 12 H12 V15'
          stroke={ disabled ? disabledStroke : stroke }
          strokeWidth={ 1.5 }
          fill='none'
        />
        <path
          d='M3 3 H13'
          stroke={ disabled ? disabledStroke : stroke }
          strokeWidth={ 1.5 }
          fill='none'
        />
        <path
          d='M3 6 H13'
          stroke={ disabled ? disabledStroke : stroke }
          strokeWidth={ 1.5 }
          fill='none'
        />
        <path
          d='M3 9 H9'
          stroke={ disabled ? disabledStroke : stroke }
          strokeWidth={ 1.5 }
          fill='none'
        />
      </g>

    </svg>
  )
};

EditIcon.propTypes = {
  disabled       : PropTypes.bool,
  height         : PropTypes.oneOfType([
    PropTypes.string, PropTypes.number,
  ]),
  width          : PropTypes.oneOfType([
    PropTypes.string, PropTypes.number,
  ]),
  stroke         : PropTypes.string,
  disabledStroke : PropTypes.string,
  onClick        : PropTypes.func,
};

EditIcon.defaultProps = {
  disabled       : false,
  width          : defaultWidth,
  height         : defaultHeight,
  stroke         : getColor(COLOR_TYPE.DEFAULT, 100),
  disabledStroke : getColor(COLOR_TYPE.DEFAULT, 50),
  onClick        : null,
};

export default EditIcon;
